#root .signup-container{
    transition: 0.5s all ease-in-out;
    
    height:87vh;
    width:40vw;
    // margin-right:10vw;
    z-index:1;
    overflow-y:auto;
    position: relative;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.4), 0 3px 10px 0 rgba(0, 0, 0, 0.4);
    border-radius: 0.3rem;

    .signup-form{
        background-color: rgb(58, 47, 47);
        color:rgb(255, 255, 255);
        overflow: hidden;
        border-radius: 0.3rem;
        height:100%;
        width:100%;
        padding:3rem;
        background-repeat: no-repeat; 
        background-position: bottom;
        font-size:1.3rem;

        input{
            color:rgb(243, 233, 233);
            font-size:1.3rem;
        }

        .header{
            height:34%;
            text-align: center;
        }

        // #root .body {
        //     height:75%;
        // }

        .user-icon{
            width:20%;
            height:20%;
        }

        .signup-button{
            width:80%;
            margin-top:1.7rem;
            transition: all 0.2s ease-in-out;
            padding:0.7rem 0 0.7rem 0;
            border-radius: 3rem;
            // font-weight: 600;
            font-size:1.2rem;
            color:rgb(241, 231, 231);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            background: linear-gradient(45deg,rgb(79, 233, 169), rgb(231, 188, 227), rgb(197, 22, 188), rgb(79, 101, 207)) ;
            // background:linear-gradient(-45deg,  #034479, #053049, rgba(173, 2, 19, 1), rgba(219, 107, 87, 0.7));
            background-size: 200% 200%;

        }
        .signup-button:hover{
            color:rgb(255, 255, 255);
            // background: linear-gradient(90deg, rgb(231, 188, 227), rgb(197, 22, 188), rgb(79, 101, 207)) ;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 12px 40px 0 rgba(0, 0, 0, 0.4);
            background-position:50% 50%;

        }
    }
}

#root .main-cont-enlarged .label, input{
    color:rgb(255, 255, 255);
}

#root .sign-up-slide-left{
    transform:translateX(0%)
}

#root .sign-up-slide-right{
    transform:translateX(300%)
}
